const headerSliderWrapper = document.querySelector('.header__content-wrapper');
const headerControls = document.querySelector('.content-control');
const reviewSliderWrapper = document.querySelector('.review-wrapper');
const reviewControls = document.querySelector('.reviews-controls');

function moveSlider(move) {
	headerSliderWrapper.style.transform = `translateX(-${move * 25}%)`;
}

function controlDeactive() {
	for( let i = 0; i < headerControls.children.length; i++ ) {
		headerControls.children[i].classList.remove('active');
	}	
}

// Слайдер в шапке
headerControls.addEventListener('click', (evt) => {
	const targetId = evt.target.getAttribute('id');
	moveSlider(targetId);
	controlDeactive();
	evt.target.classList.add('active');
})

//
reviewControls.addEventListener('click', (evt) => {
	const targetId = evt.target.dataset.move;
	const sumReviews = reviewSliderWrapper.children.length;
	const step = Math.round(100 / sumReviews);
	let currentPosition;
	for( let i = 0; i < reviewSliderWrapper.children.length; i++ ) {
		if(reviewSliderWrapper.children[i].dataset.review === 'active') {
			currentPosition = (targetId === 'next') ? i+1 : i-1;
			if(currentPosition < 0 || currentPosition > reviewSliderWrapper.children.length - 1) return;
			reviewSliderWrapper.children[i].dataset.review = '';
			reviewSliderWrapper.children[currentPosition].dataset.review = 'active';
			break;
		}

	}
	reviewSliderWrapper.style.transform = (targetId == 'next') ? `translateX(-${currentPosition * step}%)` : `translateX(-${currentPosition * step}%)`;
})

	$("#nav").on("click","a", function (event) {
		event.preventDefault();
		var id  = $(this).attr('href'),
		top = $(id).offset().top;
	$('body,html').animate({scrollTop: top}, 1500);
});

		$("#to-top").on("click", function (event) {
			event.preventDefault();
			var id  = $(this).attr('href'),
			top = $(id).offset().top;
		$('body,html').animate({scrollTop: top}, 1500);
	});